/*
* Custom Bootstrap
*/
@import url(../../node_modules/bootstrap/dist/css/bootstrap-grid.css);
/*
* Font-awesome
*/
@import url(../../node_modules/font-awesome/css/font-awesome.min.css);
body { background: #272727; }

.header-top { margin-top: 15px; align-items: center; display: flex; justify-content: space-between; flex-wrap: wtap; }

.search { display: flex; justify-content: flex-end; padding: 0; }

.navigation { padding-top: 10px; padding-left: 0; padding-right: 0; }

.main-nav { display: flex; justify-content: space-between; align-items: center; list-style: none; padding: 0; }

.main-nav li a { color: #28a745; font-weight: bold; text-decoration: none; }

.main-nav li { border: 1px solid #FFE400; border-radius: 5px; padding-left: 10px; padding-right: 10px; padding-bottom: 5px; }

.main-nav li a:hover { color: #FF652F; font-weight: bold; }

.logo { padding: 0; }

.main-text { display: flex; justify-content: flex-start; color: #FFE400; align-items: center; }

.main-text i { color: #FFE400; padding-top: 5px; }

.mainBody { background: transparent; /*border: 1px solid #28a745;*/ border-radius: 10px; margin-bottom: 15px; }

.line { height: 1px; background: #FFE400; margin-top: 15px; }

.footer-top { margin: 10px 0; }

.social { color: #28a745; padding-left: 0; }

.social a i { color: #28a745; padding-left: 5px; cursor: pointer; font-weight: 100; }

.social a i:hover { color: #FFE400; }

.contact { color: #28a745; padding: 0; display: flex; justify-content: flex-start; }

.contact a { color: #28a745; text-decoration: none; }

.contact i { padding-left: 15px; }

.contact a:hover { color: #FFE400; text-decoration: none; }

.login { display: flex; justify-content: flex-end; padding-right: 0; }

.login a { color: #28a745; text-decoration: none; padding-left: 15px; }

.login a:hover { color: #FFE400; text-decoration: none; }

.blocks { display: flex; justify-content: space-between; margin-top: 15px; line-height: 30px; }

.label { color: #EEE2DC; display: flex; justify-content: center; padding-top: 10px; }

img { width: 100%; }

.newsTitle { color: white; }

.img_show { color: white; }

.news_source { color: white; }

.news_description { color: white; }

.news_date { color: white; }

.news_conteiner { display: flex; flex-wrap: nowrap; justify-content: space-between; }

.NewsBlocks { flex-grow: 1; border: 1px solid aqua; padding: 1%; }

.weather { display: flex; }

.weather .weather_elements { background: #666666; display: flex; flex-wrap: wrap; color: #ffffff; width: 100px; margin-right: 0.5%; box-shadow: 2px 4px 7px rgba(155, 182, 190, 0.55); }

.weather .weather_elements .date { width: 100%; }

.weather .weather_elements .w_pict { width: 50%; }

.weather .weather_elements .t_diap .upper_t { width: 100%; }

.weather .weather_elements .t_diap .lower_t { width: 100%; }

.weather .weather_elements:hover { background: #f4f6fa; color: #164870; }
